@import '../../style/variables.scss';

.banner {
  display: flex;
  width: 100%;
  background-color: $dark;
  justify-content: space-between;
  margin-bottom: 49px;
  &__img {
    margin-left: 45px;
  }

  &__text {
    font-weight: 700;
    font-size: 24px;
    line-height: 28px;
    color: #ffffff;
    align-self: center;
  }

  &__logo {
    margin-right: 25px;
  }
}